* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
body {
  background-color: #fff;
  font-size: 1rem;
  line-height: normal;
  color: #000;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  min-width: 320px;
}
section {
  padding: 5rem 0;
}
section.bg {
  background-color: #d3f4fb;
}
h1 {
  position: relative;
  font-size: 4rem;
}
h2 {
  position: relative;
  font-size: 3rem;
}
h3 {
  position: relative;
  font-size: 2.5rem;
}
h4 {
  position: relative;
  font-size: 2rem;
}
h5 {
  position: relative;
  font-size: 1.5rem;
}
h6 {
  position: relative;
  font-size: 1.25rem;
}
p {
  position: relative;
  font-size: 1rem;
}
p.title {
  font-size: 3.5rem;
  text-align: center;
  text-transform: uppercase;
  color: #202020;
  padding-bottom: 1rem;
  margin-bottom: 3rem;
}
p.title:before {
  content: '';
  width: 3.75rem;
  height: 0.125rem;
  background-color: #e2e2e2;
  position: absolute;
  bottom: -0.063rem;
  left: 50%;
  transform: translateX(-50%);
}
a {
  position: relative;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  opacity: 0.75;
}
a.btn {
  position: relative;
  height: 3.75rem;
  width: 16.875rem;
  border: 1px solid #fff;
  border-radius: 1.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  color: #fff;
}
a.btn:hover {
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}
a.btn img {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.625rem;
}
.social_link {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 1.3525rem;
}
.social_link a {
  margin: 0 1.3525rem;
  color: #fff;
  height: 2.5rem;
  width: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}
.social_link a.mobile {
  display: none;
}
.container {
  width: 100%;
  margin: 0 auto;
  max-width: 91rem;
  padding: 0 0.5rem;
  margin: 0 auto;
}
.wrapper {
  width: 90%;
  max-width: 100rem;
  padding: 0 1rem;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
}
.logo {
  position: relative;
  z-index: 1;
}
header {
  width: 100%;
  height: 7.5rem;
  position: fixed;
  left: 0;
  top: 0;
  padding: 1.25rem 3rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(32,32,32,0.502);
  z-index: 1;
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
}
header .menu ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .menu ul li {
  padding: 0 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .menu ul li a {
  font-size: 1.5rem;
  font-weight: 400;
  color: #fff;
  cursor: pointer;
  position: relative;
}
header .menu ul li a:before {
  content: '';
  display: block;
  height: 0.125rem;
  width: 0;
  position: absolute;
  background-color: #fff;
  bottom: -0.25rem;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.25s linear;
}
header .menu ul li a:hover:before {
  width: 100%;
}
header .top {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: 1;
}
.slider {
  width: 100%;
  max-height: 56.25vw;
  height: 100vh;
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
  position: relative;
}
.slider .slick-list,
.slider .slick-track,
.slider .slick-slide {
  height: 100%;
}
.slider .slick-dots {
  position: absolute;
  bottom: 0;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
}
.slider .slick-dots li {
  width: 1rem;
  height: 1rem;
  margin: 0.5rem;
  border: 1px solid #fff;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
}
.slider .slick-dots li.slick-active {
  background-color: #fff;
}
.slider .slick-dots li:hover {
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}
.slider .slick-dots li button {
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}
.slider .slide {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
}
.slider .slide .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slider .slide .container p {
  font-size: 4rem;
  font-style: italic;
  font-weight: 700;
  text-align: center;
  text-shadow: 0px 0px 5px #000;
  color: #fff;
}
.slider .slide img {
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 100%;
  min-width: 100%;
}
.slider .slide:before {
  content: '';
  display: block;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #202020;
  opacity: 0.5;
}
.table {
  max-width: 75rem;
  width: 95%;
  margin: 0 auto;
  padding: 0 2.5% 2rem;
}
.table tbody > :nth-child(odd) {
  background-color: #fff;
}
.table tbody > :nth-child(even) {
  background-color: #faf9f9;
}
.table tbody > :first-child td {
  text-align: center;
  justify-content: center;
}
.table tbody > :first-child td p {
  text-align: center;
  font-weight: 700;
}
.table tr {
  display: flex;
  align-items: flex-start;
  height: 100%;
}
.table tr td {
  min-height: 3rem;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.table tr td p {
  font-size: 1.25rem;
}
.table tr > :first-child {
  flex: 0.1;
}
.table tr > :nth-child(2) {
  flex: 0.65;
  justify-content: flex-start;
}
.table tr > :nth-child(2) p {
  text-align: left;
}
.table tr > :last-child {
  flex: 0.25;
  justify-content: center;
}
.table tr > :last-child p {
  text-align: center;
}
.about_company {
  position: relative;
}
.about_company .item {
  padding: 0 1.25rem 2.5rem;
  flex: 1;
}
.about_company .item .image {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.about_company .item .image:before {
  content: '';
  width: 100%;
  display: block;
  position: relative;
  padding-top: 56.25%;
}
.about_company .item .image img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: auto;
}
.about_company .item p {
  font-size: 1.25rem;
  line-height: 1.33;
  margin-bottom: 1rem;
}
.about_company .item p span {
  font-size: 1.5rem;
}
.about_company .item p b {
  color: #f00;
}
.about_company .item ul {
  list-style-type: none;
}
.about_company .item ul li {
  margin-bottom: 0.5rem;
}
.about_company .item .share {
  display: flex;
  align-items: center;
}
.about_company .item .share p {
  flex: 0.75;
}
.about_company .item .share img {
  padding: 1rem;
  border: 1px solid #808080;
  flex-shrink: 0.25;
  margin-left: 1rem;
}
.info {
  position: relative;
}
.info .item {
  padding: 0 1.25rem 2.5rem;
  flex: 1;
  min-width: 50%;
}
.info .item .image {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.info .item .image:before {
  content: '';
  width: 100%;
  display: block;
  position: relative;
  padding-top: 56.25%;
}
.info .item .image img {
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
  min-width: 100%;
  width: auto;
}
.info .item p {
  font-size: 1.125rem;
  line-height: 1.33;
  margin-bottom: 1rem;
  text-align: left;
  padding-bottom: 0;
}
.info .item p:before {
  content: none;
}
.info .item p span {
  font-size: 1.5rem;
}
.info .item p.title {
  font-size: 1.375rem;
}
.info .item ul {
  list-style-type: none;
}
.info .item ul li {
  margin-bottom: 0.5rem;
}
.info .item .share {
  display: flex;
  align-items: center;
}
.info .item .share p {
  flex: 0.75;
}
.info .item .share img {
  padding: 1rem;
  border: 1px solid #808080;
  flex-shrink: 0.25;
  margin-left: 1rem;
}
.cert .item {
  flex: 1;
  padding: 1rem;
}
.cert .item a {
  border: 1px solid #174a79;
  padding: 0.5rem;
  width: 100%;
  display: block;
  position: relative;
}
.cert .item a:before {
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-shadow: inset 0px 0px 10px 0px #fff;
  position: absolute;
}
.cert .item a img {
  width: 100%;
}
.price .wrapper {
  max-width: 75rem;
  width: 95%;
  margin: 0 auto;
  padding: 0 2.5%;
}
footer {
  padding: 1.875rem 0.5rem 1rem;
  background-color: #3f95ba;
  color: #fff;
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
}
footer .copyright {
  font-size: 1.125rem;
  text-align: center;
}
footer .container {
  max-width: 64rem;
  padding: 0 0.5rem;
}
footer .contacts {
  flex-shrink: 0;
  width: 93.75%;
  margin: 1rem auto 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
footer .contacts a {
  font-size: 1.125rem;
  display: flex;
  align-items: baseline;
  color: #fff;
}
footer .contacts a i {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
}
footer .top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
}
footer .top:before {
  content: '';
  position: absolute;
  bottom: 0;
  background-color: #174a79;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  width: 93.75%;
}
/* include libs styles*/
@import 'https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap';
@media screen and (max-width: 1200px) {
  a.btn {
    font-size: 0;
    width: 3.75rem;
  }
  a.btn img {
    margin: 0;
  }
  p.title {
    margin-bottom: 1.5rem;
  }
  section {
    padding: 2rem 0;
  }
  header {
    background-color: #3f95ba;
    position: relative;
    height: auto;
    flex-wrap: wrap;
  }
  .social_link > :last-child {
    margin-right: 0;
  }
  .social_link a {
    margin-left: 0;
    width: 3.75rem;
    height: 3.75rem;
    border: 1px solid #fff;
    border-radius: 50%;
  }
  .social_link a.mobile {
    display: flex;
  }
  .wrapper {
    width: 100%;
    padding: 0 0.5rem;
  }
  .about_company .item,
  .info .item {
    padding: 0 0 1rem;
    width: 100%;
    flex-shrink: 0;
    flex: none;
  }
  .cert .item {
    padding: 0;
    margin: 0 0.25rem;
  }
  .cert > :first-child {
    margin-left: 0;
  }
  .cert > :last-child {
    margin-right: 0;
  }
  .slider .slide .container p {
    font-size: 2rem;
  }
}
@media screen and (max-width: 680px) {
  header {
    justify-content: center;
    flex-direction: column;
    padding: 1.25rem 0;
  }
  header .social_link {
    margin-top: 0.75rem;
    margin-left: 0;
  }
  header .social_link a {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.25rem;
  }
  a.btn {
    width: 2.5rem;
    height: 2.5rem;
    margin-top: 0.75rem;
  }
  p.title {
    font-size: 2rem;
  }
  .about_company .item .share {
    flex-direction: column-reverse;
  }
  .about_company .item .share p {
    text-align: center;
  }
  .slider {
    height: 100vw;
    max-height: 100vw;
  }
  .slider p {
    font-size: 1.25rem;
  }
  .table {
    width: 100%;
  }
  .table tbody tr td {
    padding: 0.25rem;
  }
  .table tbody tr td p {
    font-size: 0.875rem;
  }
  footer .contacts {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 2rem;
  }
  footer .contacts a {
    margin-bottom: 1rem;
  }
  footer .contacts > :last-child {
    margin-bottom: 0;
  }
}
